import React from 'react';
import { useEffect, useState } from "react";
import "./Authorization.css"
import phantomLogo from "./image/phantom-logo.png"
import disconnectLogo from "./image/disconnect.png"
import { useDispatch, useSelector } from 'react-redux';
import { initializeApp } from 'firebase/app';
import { getDatabase, ref, set, child, get } from "firebase/database";
import { getApps, getApp } from "firebase/app";
import Nickname from './Nickname';
  

const Authorization = () => {

    const dispatch = useDispatch()
    let isNickname = useSelector(state => state.getNicknameReducer.isNickname)
    const [provider, setProvider] = useState();
    const [walletKey, setWalletKey] = useState();
    // const firebaseConfig = {
    //     apiKey: "AIzaSyBycQoE5ODQa4JkXDXU5INlM5kDNNA9cHo",
    //     authDomain: "petproject-d4ac7.firebaseapp.com",
    //     databaseURL: "https://petproject-d4ac7-default-rtdb.europe-west1.firebasedatabase.app",
    //     projectId: "petproject-d4ac7",
    //     storageBucket: "petproject-d4ac7.appspot.com",
    //     messagingSenderId: "374859257345",
    //     appId: "1:374859257345:web:ef1afbf6878dc61da11200"
    // };
    // const app = initializeApp(firebaseConfig);
    const database = getDatabase();

    const getProvider = () => {
    if ("solana" in window) {
        const provider = window.solana;
        if (provider.isPhantom) return provider;
        }
    };

    useEffect(() => {
        const provider = getProvider();   
        if (provider) setProvider(provider);
        else setProvider(undefined);
    }, []);

    const connectWallet = async () => {
        const { solana } = window;

        if (solana) {
          try {
            const response = await solana.connect();
            console.log('wallet account ', response.publicKey.toString());
            setWalletKey(response.publicKey.toString());
            localStorage.setItem('WalletKey', response.publicKey.toString())
            dispatch({type: 'auth'})
            writeUserData()
            getNickName()
          } catch (err) {
           // { code: 4001, message: 'User rejected the request.' }
          }
        }     
    };

    const disconnectWallet = async () => {
        const { solana } = window;
    
        if (walletKey && solana) {
          await (solana).disconnect();
          setWalletKey(undefined);
          localStorage.removeItem('WalletKey')
        }
    };

    function writeUserData() {
      const db = getDatabase();
      const dbRef = ref(getDatabase());
      get(child(dbRef, "/")).then((snapshot) => {
        if (snapshot.exists()) {
          const user = localStorage.getItem('WalletKey')
          const users = snapshot.val()
          if (!users[user]) {
            set(ref(db, `${localStorage.getItem('WalletKey')}/`), {
              username: localStorage.getItem('WalletKey'),
            });
            getNickName()
          }
        } else {
          console.log("No data available");
          }
        }).catch((error) => {
          console.error(error);
        });
      } 
    
    function getNickName() {
      const dbRef = ref(getDatabase());
      get(child(dbRef, "/")).then((snapshot) => {
      if (snapshot.exists()) {
        const user = localStorage.getItem('WalletKey')
        const users = snapshot.val()
        if (users[user].nickname) {
          // console.log(users[user].nickname)
        } else {
          dispatch({type: 'enterNickname'})
        }
      } else {
        console.log("No data available");
        }
      }).catch((error) => {
        console.error(error);
      });
    }




    return (
        <div className='authorization_container'>
            <div className='authorization_wrap'>
              <button className='btn' onClick={connectWallet}>
                <div className='btn_phantom'>
                  <img className='authorization_phantomLogo' src={phantomLogo} alt="phantomLogo" width="25" height="25" />
                  Phantom
                  {
                    walletKey
                    ? "     " + walletKey.slice(0, 3) +"..."+ walletKey.slice(walletKey.length - 3, walletKey.length)
                    : ''
                  }
                  {walletKey && (
                    <div>
                      <button className='authorization_disconnectLogo' onClick={disconnectWallet}>
                        <img src={disconnectLogo} alt="disconnectLogo" width="20" height="20" />
                      </button>
                    </div>
                  )}  
                </div>                            
              </button>
              {
                (!isNickname)
                ? <div></div>
                : <Nickname />
              }
              
            </div>
        </div>
    );
};

export default Authorization;