const defaultNicknameState = {
    isNickname: false
}

export const getNicknameReducer = (state  = defaultNicknameState, action) => {
    switch (action.type) {
        case "enterNickname":
            return {...state, 
                isNickname: true
                }
        
        case "haveNickname":
            return {...state, 
                isNickname: false
                }

        default:
            return state
        }
}